/*-------------------------
    Variables
-------------------------*/
:root {
  --color-primary: #5956E9;
  --color-white: #ffffff;
  --color-black: #000000;
  --color-light: #ECF2F6;
  --color-dark: #27272E;
  --color-text-dark: #292930;
  --color-accent1: #FFDC60;
  --color-accent2: #FAB8C4;
  --color-blue-shade: #6865FF;
  --color-link: #2522BA;
  --color-mabel: #DBF8FF;
  --color-fog: #DBDEFF;
  --color-pink-shade: #FFD3DB;
  --color-peach: #FFEDDC;
  --color-rose: #C75C6F;
  --color-chart1: #896BA7;
  --color-chart2: #BADEFF;
  --color-body: #525260;
  --color-gray-1: #757589;
  --color-gray-2: #999FAE;
  --color-gray-3: #999AB8;
  --color-gray-4: #99A1AA;
  --color-ship-gray: #42424A;
  --color-ghost: #C7C7D5;
  --color-mercury: #E5E5E5;
  --gradient-primary: linear-gradient(90deg, #ECF2F6 0%, rgba(236, 242, 246, 0) 70.31%);
  --gradient-blue: linear-gradient(145.92deg, #5956E9 20.18%, #9991FF 76.9%);
  --gradient-accent: linear-gradient(180deg, #FAB8C4 0%, #FFEDF0 100%);
  --gradient-white: linear-gradient(266.3deg, rgba(236, 242, 246, 0) 7.84%, #ECF2F6 29.1%, rgba(236, 242, 246, 0) 64.32%);
  --gradient-dark: linear-gradient(180deg, #27272E 0%, #303035 100%);
  --border-light: 1px solid #E3E6E9;
  --border-lighter: 1px solid #ECF2F6;
  --border-dark: 1px solid var(--color-ship-gray);
  --border-gray: 1px solid var(--color-gray-4);
  --font-primary: "DM Sans", sans-serif;
  --font-secondary: "Poppins", sans-serif;
  --font-awesome: "Font Awesome 5 Pro";
  --transition: all 0.3s ease-in-out;
  --font-body-1: 18px;
  --font-body-2: 16px;
  --font-body-3: 14px;
  --font-body-4: 12px;
  --line-height-b1: 1.5;
  --line-height-b3: 1.1;
  --h1: 80px;
  --h2: 64px;
  --h3: 48px;
  --h4: 32px;
  --h5: 24px;
  --h6: 20px;
}

/*-------------------------
    Reset Styels
-------------------------*/
a {
  display: inline-block;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  transition: var(--transition);
}

span {
  display: inline-block;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

ul.list-style,
ol.list-style {
  margin: -10px 0;
}
ul.list-style li,
ol.list-style li {
  margin: 10px 0;
}
ul.list-style li::marker,
ol.list-style li::marker {
  color: #5956E9;
  color: var(--color-primary);
}

.list-unstyled {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
}

.container, .container-fluid, .container-lg, .container-md, .container-sm, .container-xl, .container-xxl {
  padding-left: 15px;
  padding-right: 15px;
}

@media only screen and (min-width: 1200px) and (max-width: 1399px) {
  .container {
    max-width: 100%;
    padding-left: 30px;
    padding-right: 30px;
  }
}
.row {
  margin-left: -15px;
  margin-right: -15px;
}
.row > [class*=col] {
  padding-left: 15px;
  padding-right: 15px;
}
.row.g-0 {
  margin-left: 0;
  margin-right: 0;
}
.row.g-0 > [class*=col] {
  padding-left: 0;
  padding-right: 0;
}

@media only screen and (min-width: 1400px) {
  .row-35 {
    margin-left: -35px;
    margin-right: -35px;
  }
  .row-35 > [class*=col] {
    padding-left: 35px;
    padding-right: 35px;
  }
  .row-40 {
    margin-left: -40px;
    margin-right: -40px;
  }
  .row-40 > [class*=col] {
    padding-left: 40px;
    padding-right: 40px;
  }
  .row-45 {
    margin-left: -45px;
    margin-right: -45px;
  }
  .row-45 > [class*=col] {
    padding-left: 45px;
    padding-right: 45px;
  }
}
